* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}
ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: 100%;
  margin: 0 auto;
  margin-top: 50px;

  padding: 0 10px 0 10px;
}

.search-form {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;

  background: rgb(157, 180, 159);
  padding: 20px 0 20px 0;
}

.search-form__input {
  width: 180px;
  padding: 10px;
  height: 30px;

  font-size: 14px;
  border: none;
  border-radius: 5px;
  margin-right: 3px;
}

.search-form__button {
  height: 30px;
  width: 30px;

  border-radius: 5px;
  font-size: 14px;

  border: none;

  cursor: pointer;
}
.search-form__button:hover {
  background-color: rgb(207, 207, 207);
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

.photo-card {
  margin-bottom: 15px;
  width: 350px;
  margin-right: 5px;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
}

.photo-card img {
  min-height: 275px;
}

.info {
  max-height: 150px;
  display: flex;
  justify-content: space-around;
  align-items: center;

  padding: 10px;
}

.info-item {
  display: flex;

  text-align: center;
  flex-direction: column;
}

.load-more {
  width: 150px;
  height: 40px;
  cursor: pointer;

  font-size: 14px;

  margin-left: 50%;
  margin-bottom: 50px;
  transform: translateX(-50%);
  background: rgb(48, 136, 48);
  color: white;

  border: none;
  border-radius: 5px;
}
.load-more:hover {
  background-color: #1c6428;
}
.load-more.is-hidden {
  display: none;
}

.final-text {
  text-align: center;
  font-size: 18px;

  margin: 20px 0 20px 0;
}
.final-text.is-hidden {
  display: none;
}
